/**
 * ç»˜åˆ¶æ°´å¹³ç›´çº¿
 * @param ctx
 * @param y
 * @param left
 * @param right
 */
export function renderHorizontalLine (ctx, y, left, right) {
  ctx.beginPath()
  const correction = (ctx.lineWidth % 2) ? 0.5 : 0
  ctx.moveTo(left, y + correction)
  ctx.lineTo(right, y + correction)
  ctx.stroke()
  ctx.closePath()
}

/**
 * ç»˜åˆ¶å‚ç›´ç›´çº¿
 * @param ctx
 * @param x
 * @param top
 * @param bottom
 */
export function renderVerticalLine (ctx, x, top, bottom) {
  ctx.beginPath()
  const correction = (ctx.lineWidth % 2) ? 0.5 : 0
  ctx.moveTo(x + correction, top)
  ctx.lineTo(x + correction, bottom)
  ctx.stroke()
  ctx.closePath()
}

/**
 * ç»˜åˆ¶çº¿
 * @param ctx
 * @param renderFuc
 */
export function renderLine (ctx, renderFuc) {
  ctx.save()
  if (ctx.lineWidth % 2) {
    ctx.translate(0.5, 0.5)
  }
  renderFuc()
  ctx.restore()
}
